import axios from 'axios'
import router from '../router'

const state = {
    res:null,
    sequences: JSON.parse(localStorage.getItem("sequences"))
}

const getters = {}

const mutations = {}

const actions = {
    startExam({rootState}){
        rootState.loading = true
        axios
            .get('http://exam.tmcindonesia.com/get.php?json=true')
            .then(res=>{
                localStorage.setItem("sequences",JSON.stringify(res.data))
                rootState.loading = false
                router.push({name:'sequence'})
                location.reload()
            })
    },
    sendUserSequence({rootState},data){
        rootState.loading = true
        axios
            .post(process.env.VUE_APP_URL+'participant/exam/send-user-sequence',data,{
                headers:{
                    'Authorization':rootState.userLoggedIn.token,
                    'Content-Type':'application/json'
                }
            })
    },
    finishExam({rootState},data){
        rootState.loading = true
        axios
            .post(process.env.VUE_APP_URL+'participant/exam/finish',data,{
                headers:{
                    'Authorization':rootState.userLoggedIn.token,
                }
            })
    },
}

export default{
    state,getters,mutations,actions,namespaced : true
}